.min-height {
    height: 120px;
}

.main{
    margin-top: 80px;
}

.not-caught {
    margin: 20px 0;
}

.not-caught input {
    margin-right: 6px;
}

img.full-width {
    width: 100%;
}

.isCaught .card {
    background-color: #55bd5529 !important;
}

.done {
    background-color: #55bd5529 !important;
}
.img-zoom-lens {
    position: absolute;
    border: 1px solid #d4d4d4;
    /*set the size of the lens:*/
    width: 40px;
    height: 40px;
}
#image-map {
    vertical-align: top;
}
.img-zoom-container {
    position: relative;
}

.img-zoom-result {
    border: 1px solid #d4d4d4;
    /*set the size of the result div:*/
    display:inline-block;
    width: 600px;
    height: 600px;
}
.bank-item img{
    width: 100%!important;
    cursor:pointer;
}

.bank-card {
    min-height: 96px;
    min-width: 96px;
    position:relative;
}

.ui.grid>*{
    padding-right: .5rem;
    padding-left: .5rem;
    margin-top: 1rem!important;
}

.ui.grid{
    margin-top: 0!important;
    margin-bottom: 1rem!important;
}

.isCaught .bank-card {
    background-color: #55bd5529!important;
}

.isNotCatchable .bank-card {
    background-color: #ff000033!important;
}


.isNotCaught .bank-card img{
    -webkit-filter: brightness(0);
            filter: brightness(0);
    opacity: .1;
}

.ui.circular.label {
    position: absolute;
    top: -10px;
    right: -10px;
}
.image {
    background-color: #dcdcdc30 !important;
    border-radius: 5px;
}

.float-right{
    float:right;
}

.isCaught .card {
    background-color: #55bd5529!important;
}

.isNotCatchable .card {
    background-color: #ff000033!important;
}

.input-height{
    height:19px!important;
}

.notes label {
    margin-right: 10px;
}

.minus-button {
    padding-right: 10px;
}

.showForm{
    display:block;
}

.hideForm{
    display:none;
}
